import { useTranslation } from "react-i18next";

export interface InputProps
  extends React.InputHTMLAttributes<HTMLInputElement> {
  label: string;
  text: string;
  isChallenge?: boolean;
}

export default function Text({
  isChallenge = false,
  label,
  text,
  ...props
}: InputProps) {
  const { i18n } = useTranslation();
  const textColor = isChallenge ? "textcolor" : "";

  return (
    <div className="grid grid-cols-1 gap-2">
      <label
        htmlFor={props.name}
        className={`${
          i18n.language === "en"
            ? `font-en text-curren mobile:text-base text-lg text-${textColor}`
            : `text-curren text-2xl font-arabic_normal mobile:text-2xl text-${textColor}`
        }`}
      >
        {label}
      </label>
      <div className="text-xl text-placeholder font-normal font-roboto opacity-75">
        {text}
      </div>
    </div>
  );
}
