import { getDocs, limit, orderBy, query } from "firebase/firestore";
import { useEffect, useState } from "react";
import Text from "src/components/common/text-coponent";
import { InitialValuesType } from "src/core/types/user.type";
import { contactForm } from "src/firebase";

export default function InfoFormContainer() {
  const [projects, setProjects] = useState<Array<InitialValuesType>>();
  async function fechData() {
    try {
      const contactQuery = query(
        contactForm,
        orderBy("date", "desc"),
        limit(1)
      );
      const querySnapshot = await getDocs(contactQuery);
      const fetchedData: Array<InitialValuesType> = [];

      querySnapshot.forEach((doc) => {
        fetchedData.push({ id: doc.id[0], ...doc.data() } as InitialValuesType);
      });
      // eslint-disable-next-line no-console
      console.log(fetchedData);
      setProjects(fetchedData);
    } catch (error) {
      // eslint-disable-next-line no-console
      console.error("Error fetching data:", error);
    }
  }

  useEffect(() => {
    fechData();
  }, []);
  return (
    <div className="gap-[82px] flex flex-col w-full">
      <div className="gap-[50px] flex flex-col items-center">
        <h1 className="text-textcolor font-roboto text-5xl">My Projects</h1>
        <div className="w-full">
          <div>
            <h1 className="text-textcolor font-roboto text-2xl pb-9">
              Personal Detail
            </h1>
            <div className="w-full flex pb-9 gap-[50px]">
              <label className="flex flex-col w-full">
                <Text
                  label={"Name"}
                  text={projects ? projects[projects.length - 1].name : ""}
                  isChallenge
                  name={"Enter Full Name"}
                />
              </label>
              <label className="flex flex-col w-full">
                <Text
                  label="Email"
                  text={projects ? projects[projects.length - 1].email : ""}
                  isChallenge
                  name={"m.nawaz123@example.com"}
                />
              </label>
            </div>
            <div className="w-full flex pb-9 gap-[50px]">
              <label className="flex flex-col w-full">
                <Text
                  label="Phone Number"
                  text={projects ? projects[projects.length - 1].phone : ""}
                  isChallenge
                  name={"Phone Number"}
                />
              </label>
              <label className="flex flex-col w-full">
                <Text
                  label="Your Project"
                  text={projects ? projects[projects.length - 1].project : ""}
                  isChallenge
                  name={"Your Project"}
                />
              </label>
            </div>
            <div className="w-full flex flex-row items-center gap-6">
              {projects
                ? projects[projects.length - 1].upload
                : "NNNNNNNNOOOOOOO"}
              {projects
                ? projects[projects.length - 1].upload.map(
                    (uploadItem, uploadIndex) => (
                      <img
                        src={uploadItem}
                        className="h-[410px] w-[189.347px]"
                        key={uploadIndex}
                      />
                    )
                  )
                : null}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
