import { addDoc, onSnapshot, Timestamp } from "firebase/firestore";
import { getDownloadURL, ref, uploadBytes } from "firebase/storage";
import { Form, Formik, useFormik } from "formik";
import { useEffect, useState } from "react";
import { FiDownload } from "react-icons/fi";
import { useNavigate } from "react-router-dom";
import Input from "src/components/common/input";
import { initialValues } from "src/core/constants";
import { InputEnum } from "src/core/enums/example.enum";
import { InitialValuesType } from "src/core/types/user.type";
import { contactForm, storage } from "src/firebase";
import * as Yup from "yup";

export default function FormContainer() {
  const [project, setProject] = useState<Array<InitialValuesType>>([
    initialValues,
  ]);
  const [inputData, setInputData] =
    useState<Partial<InitialValuesType>>(initialValues);
  const [upload, setUpload] = useState<string[]>([]);
  const handleSubmit = async () => {
    try {
      const newProj: Partial<InitialValuesType> = {
        name: formik.values.name,
        email: formik.values.email,
        project: formik.values.project,
        phone: formik.values.phone,
        upload: upload,
        date: Timestamp.fromDate(new Date()),
      };
      const docRef = await addDoc(contactForm, newProj);
      setProject({ ...project, id: docRef.id, ...newProj });
    } catch (error) {
      alert(error);
    }
  };
  const handelImageChange = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const files = e.target.files;
    if (files && files.length > 0) {
      for (let i = 0; i < files.length; i++) {
        const image = files[i];
        if (image) {
          try {
            const storageRef = ref(storage, "images/" + image.name);
            await uploadBytes(storageRef, image);
            const downloadURL = await getDownloadURL(storageRef);
            setUpload((prevUpload) => [...prevUpload, downloadURL]); // Push the downloadURL into the upload state array
          } catch (error) {
            alert(error);
          }
        }
      }
    }
  };

  const handleInputChange = (field: InputEnum, value: string | string[]) => {
    setInputData({ ...inputData, [field]: value });
  };
  const validationSchema = Yup.object({
    name: Yup.string()
      .min(2, "Too Short!")
      .max(50, "Too Long!")
      .required("Required"),
    massage: Yup.string()
      .min(3, "Too Short!")
      .max(300, "Too Long!")
      .required("Required"),
    email: Yup.string().email("Invalid email").required("Required"),
  });

  const formik = useFormik({
    initialValues: initialValues,
    onSubmit: async () => {
      formik.resetForm();
    },
    validationSchema: validationSchema,
  });

  const nav = useNavigate();
  useEffect(() => {
    onSnapshot(contactForm, () => setProject(project));
  }, []);

  return (
    <div className="flex flex-col w-full">
      <div className="gap-[71px] flex flex-col items-center">
        <h1 className="text-textcolor font-roboto text-5xl">My Projects</h1>
        <div className="w-full">
          <h1 className="text-textcolor font-roboto text-2xl pb-9">
            Personal Detail
          </h1>
          <Formik
            initialValues={initialValues}
            onSubmit={() => formik.submitForm()}
            validationSchema={formik.initialValues}
            enableReinitialize
          >
            <Form
              onSubmit={(e) => {
                e.preventDefault();
                handleSubmit().then(() => {
                  nav("getInformation");
                });
              }}
              className="w-full"
            >
              <div className="w-full flex pb-9 gap-[50px]">
                <label className="flex flex-col w-full">
                  <Input
                    label={"Name"}
                    name="name"
                    placeholder="Enter Full Name"
                    isChallenge
                    onChange={(e) => {
                      formik.handleChange(e);
                      handleInputChange(InputEnum.name, e.target.value);
                    }}
                    className={`bg-colorbox py-5 px-3 placeholder:text-placeholder placeholder:font-roboto placeholder:font-normal placeholder:text-base placeholder:opacity- rounded-2xl font-medium ${
                      formik.touched.email && formik.errors.email
                        ? "border-red-600"
                        : "shadow-custom"
                    }`}
                  />
                  {formik.touched.name && formik.errors.name && (
                    <p className="text-sm mobile:text-sm text-red-900">
                      {formik.errors.name}
                    </p>
                  )}
                </label>
                <label className="flex flex-col w-full">
                  <Input
                    label="Email"
                    placeholder="m.nawaz123@example.com"
                    name="email"
                    isChallenge
                    onChange={(e) => {
                      formik.handleChange(e);
                      handleInputChange(InputEnum.email, e.target.value);
                    }}
                    className={`bg-colorbox py-5 px-3 placeholder:text-placeholder placeholder:font-roboto placeholder:font-normal placeholder:text-base placeholder:opacity- rounded-2xl font-medium ${
                      formik.touched.email && formik.errors.email
                        ? "border-red-600"
                        : "shadow-custom"
                    }`}
                  />
                  {formik.touched.email && formik.errors.email && (
                    <p className="text-sm mobile:text-sm text-red-900">
                      {formik.errors.email}
                    </p>
                  )}
                </label>
              </div>
              <div className="w-full flex pb-9 gap-[50px]">
                <label className="flex flex-col w-full">
                  <Input
                    label="Phone Number"
                    name="phone"
                    isChallenge
                    onChange={(e) => {
                      formik.handleChange(e);
                      handleInputChange(InputEnum.phone, e.target.value);
                    }}
                    placeholder="Your Project"
                    className={`bg-colorbox py-5 px-3 placeholder:text-placeholder placeholder:font-roboto placeholder:font-normal placeholder:text-base placeholder:opacity- rounded-2xl font-medium ${
                      formik.touched.email && formik.errors.email
                        ? "border-red-600"
                        : "shadow-custom"
                    }`}
                  />
                  {formik.touched.email && formik.errors.email && (
                    <p className="text-sm mobile:text-sm text-red-900">
                      {formik.errors.email}
                    </p>
                  )}
                </label>
                <label className="flex flex-col w-full">
                  <Input
                    label="Your Project"
                    name="project"
                    isChallenge
                    onChange={(e) => {
                      formik.handleChange(e);
                      handleInputChange(InputEnum.project, e.target.value);
                    }}
                    placeholder="Your Project"
                    className={`bg-colorbox py-5 px-3 placeholder:text-placeholder placeholder:font-roboto placeholder:font-normal placeholder:text-base placeholder:opacity- rounded-2xl font-medium ${
                      formik.touched.email && formik.errors.email
                        ? "border-red-600"
                        : "shadow-custom"
                    }`}
                  />
                  {formik.touched.email && formik.errors.email && (
                    <p className="text-sm mobile:text-sm text-red-900">
                      {formik.errors.email}
                    </p>
                  )}
                </label>
              </div>
              <div className="w-full flex flex-row items-center">
                <input
                  type="file"
                  name="upload"
                  id="custom-input"
                  onChange={(e) => {
                    formik.handleChange(e);
                    handelImageChange(e);
                  }}
                  hidden
                  multiple
                />
                <div className="flex justify-center cursor-pointer items-center gap-4 w-full bg-colorbox self-stretch shadow-custom px-10 py-4 placeholder:text-secondary rounded-2xl font-medium">
                  <label
                    htmlFor="custom-input"
                    className="text-smcursor-pointer font-roboto text-base text-placeholder"
                  >
                    Upload
                  </label>
                  <FiDownload className="text-buttoncolor h-6 w-6" />
                </div>
              </div>
              <button
                type="submit"
                className={`mt-[104px] w-full bg-gradient-to-r from-gradient1 to-gradient2 text-center px-16 py-4 text-white text-2xl font-roboto font-medium rounded-2xl shadow-button`}
              >
                Send
              </button>
            </Form>
          </Formik>
        </div>
      </div>
    </div>
  );
}
